
import {defineComponent} from "vue";
import {fade} from '@/utils/colors';

export default defineComponent({
  name: "total-card",
  props: {
    color: String,
    number: Number,
    label: String,
    subNumber: Number,
    subLabel: String,
  },
  computed: {
    alphaColor(): string {
      return fade(this.color!, 0.05);
    }
  }
})
